'use client';

import React, { useState } from 'react';

import { useRouter } from 'next/navigation';

import Link from 'next/link';

import { motion, AnimatePresence } from 'framer-motion';

import { Menu, X } from 'lucide-react';

type NavItem = {

  label: string;

  href?: string;

  submenu?: { label: string; href: string }[];

};

const navItems: NavItem[] = [

  { label: 'Home', href: '/' },

  {

    label: 'Services',

    href: '/services', // Fixed: removed '/Service' from the path

    submenu: [

      { label: 'Web Development', href: '/services/web' },

      { label: 'Mobile Apps', href: '/services/mobile' },

      { label: 'UI/UX Design', href: '/services/design' },

    ],

  },

  { label: 'About', href: '/about' },

  { label: 'Contact', href: '/contact' },

];

const Navbar: React.FC = () => {

  const router = useRouter();

  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const [hoveredMenu, setHoveredMenu] = useState<string | null>(null);

  const handleNavigation = (href: string) => {

    setMobileMenuOpen(false);

    router.push(href);

  };

  // Handle click for items with submenus

  const handleItemClick = (item: NavItem, e: React.MouseEvent) => {

    if (item.submenu) {

      e.preventDefault(); // Prevent default navigation for items with submenus

      return;

    }

    if (item.href) {

      handleNavigation(item.href);

    }

  };

  return (
    <nav className="bg-white/70 shadow backdrop-blur-sm sticky top-0 z-50 w-full">
      <div className="w-full px-4 sm:px-6 lg:px-20 flex items-center justify-between h-16">

        {/* Logo */}
        <Link href="/" className="text-xl font-bold text-black cursor-pointer">

          Zinfog
        </Link>

        {/* Desktop Menu */}
        <ul className="hidden md:flex items-center space-x-8">

          {navItems.map((item) => (
            <li

              key={item.label}

              className="relative group cursor-pointer"

              onMouseEnter={() => setHoveredMenu(item.label)}

              onMouseLeave={() => setHoveredMenu(null)}
            >

              {item.submenu ? (

                // For items with submenu, use span to prevent navigation
                <span

                  onClick={(e) => handleItemClick(item, e)}

                  className="text-black hover:text-blue-600 transition cursor-pointer"
                >

                  {item.label}
                </span>

              ) : (

                // For items without submenu, use Link for proper Next.js navigation
                <Link

                  href={item.href || '#'}

                  className="text-black hover:text-blue-600 transition"
                >

                  {item.label}
                </Link>

              )}

              {/* Dropdown */}

              {item.submenu && hoveredMenu === item.label && (
                <motion.ul

                  initial={{ x: -20, opacity: 0 }}

                  animate={{ x: 0, opacity: 1 }}

                  exit={{ x: -20, opacity: 0 }}

                  transition={{ duration: 0.3, ease: 'easeInOut' }}

                  className="absolute top-full left-0 mt-2 w-56 bg-white border shadow rounded z-20"
                >

                  {item.submenu.map((sub) => (
                    <li key={sub.label}>
                      <Link

                        href={sub.href}

                        className="block px-4 py-2 text-sm text-black hover:bg-gray-100 cursor-pointer"

                        onClick={() => setMobileMenuOpen(false)}
                      >

                        {sub.label}
                      </Link>
                    </li>

                  ))}
                </motion.ul>

              )}
            </li>

          ))}
        </ul>

        {/* Mobile Toggle Button */}
        <div className="md:hidden">
          <button onClick={() => setMobileMenuOpen((prev) => !prev)}>

            {mobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>

      {/* Mobile Dropdown Menu */}
      <AnimatePresence>

        {mobileMenuOpen && (
          <motion.div

            initial={{ height: 0, opacity: 0 }}

            animate={{ height: 'auto', opacity: 1 }}

            exit={{ height: 0, opacity: 0 }}

            transition={{ duration: 0.3 }}

            className="md:hidden bg-white border-t"
          >
            <ul className="flex flex-col space-y-2 p-4">

              {navItems.map((item) => (
                <li key={item.label}>

                  {item.submenu ? (
                    <span className="block text-black py-2 cursor-pointer font-medium">

                      {item.label}
                    </span>

                  ) : (
                    <Link

                      href={item.href || '#'}

                      className="block text-black py-2 cursor-pointer"

                      onClick={() => setMobileMenuOpen(false)}
                    >

                      {item.label}
                    </Link>

                  )}

                  {/* Mobile Submenu */}

                  {item.submenu && (
                    <ul className="ml-4 mt-1 space-y-1">

                      {item.submenu.map((sub) => (
                        <li key={sub.label}>
                          <Link

                            href={sub.href}

                            className="block text-sm text-gray-700 hover:text-blue-600 cursor-pointer"

                            onClick={() => setMobileMenuOpen(false)}
                          >

                            {sub.label}
                          </Link>
                        </li>

                      ))}
                    </ul>

                  )}
                </li>

              ))}
            </ul>
          </motion.div>

        )}
      </AnimatePresence>
    </nav>

  );

};

export default Navbar;



